<script lang="ts">
    export let disabled = false;
    export let checked = false;
    export let minus = false;
    export let classes = '';
    export let onChange = null;
  
    const changeMs: number = 0;
  </script>
  
  <label class:disabled class="label {classes}">
    <input
      class="real-checkbox"
      on:change
      on:change="{() => onChange && setTimeout(() => onChange(checked), changeMs)}"
      bind:checked
      {disabled}
      type="checkbox" />
    <div class="wrap">
      <span class:minus class="checkbox"></span>
    </div>
    <div>
      <slot />
    </div>
  </label>
  
  <style>
    .label {
      display: flex;
      align-items: center;
      cursor: pointer;
    }
    .label.disabled {
      opacity: 0.65;
      cursor: default;
    }
    .wrap {
      padding: 0;
      margin: 0;
      margin-right: 7px;
    }
    .real-checkbox {
      position: absolute;
      opacity: 0;
      width: 20px;
      height: 20px;
    }
    .checkbox {
      position: relative;
      width: 18px;
      height: 18px;
      display: inline-flex;
      background-color: #fff;
      border: 1px solid #e8e8e8;
      border-radius: 3px;
      background-repeat: no-repeat;
    }
  
    .real-checkbox:disabled ~ .wrap .checkbox {
      background-color: #f1f1f1;
    }
  
    .real-checkbox:checked ~ .wrap .checkbox:not(.minus) {
      background-image: url(data:image/svg+xml;base64,);
      background-size: 70%;
      background-position: center;
    }
    .checkbox.minus {
      background-image: url(data:image/svg+xml;base64,);
      background-size: 75%;
      background-position: 75% 60%;
    }
  </style>
  